// app/news/page.tsx
'use client';

import { useEffect, useState } from 'react';

type Article = {
  title: string;
  description?: string;
  url?: string;
  source?: string;
  urlToImage?: string;
  publishedAt?: string;
};

export default function NewsPage() {
  const [articles, setArticles] = useState<Article[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    fetch('/api/news')
      .then(async (res) => {
        if (!res.ok) {
          const err = await res.json().catch(() => ({}));
          throw new Error(err?.error || 'Error al cargar noticias');
        }
        return res.json();
      })
      .then((data) => {
        const raw = data.articles || [];
        const normalized = raw.map((a: any) => ({
          title: a.title || '',
          description: a.description || '',
          url: a.url || '',
          source: (a.source && (a.source.name || a.source)) || a.source || '',
          urlToImage: a.urlToImage || a.image || '',
          publishedAt: a.publishedAt || ''
        }));
        setArticles(normalized);
      })
      .catch((err) => console.error('fetch /api/news error', err))
      .finally(() => setLoading(false));
  }, []);

  const filteredArticles = articles.filter((a) => a.title.toLowerCase().includes(searchQuery.toLowerCase()));

  if (loading) return <p>Cargando noticias...</p>;

  return (
    <main style={{ padding: '16px' }}>
      <h1 style={{ textAlign: 'center', marginBottom: '16px' }}>Noticias</h1>

      {/* Barra de búsqueda */}
      <div style={{ textAlign: 'center', marginBottom: '16px' }}>
        <input
          type="text"
          placeholder="Buscar noticias..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          style={{ width: '80%', maxWidth: '400px', padding: '8px', borderRadius: '4px', border: '1px solid #ccc' }}
        />
      </div>

      {/* Tarjetas de noticias */}
      <div style={{ display: 'grid', gap: '16px', gridTemplateColumns: 'repeat(auto-fill, minmax(250px, 1fr))' }}>
        {filteredArticles.map((article, idx) => (
          <div key={idx} style={{ border: '1px solid #ccc', borderRadius: '8px', overflow: 'hidden', padding: '8px' }}>
            {article.urlToImage && <img src={article.urlToImage} alt={article.title} style={{ width: '100%', height: '150px', objectFit: 'cover' }} />}
            <h3>{article.title}</h3>
            <p>{article.description}</p>
            <p style={{ fontSize: '0.8rem', color: '#555' }}>{article.source}</p>
            {article.url && <a href={article.url} target="_blank" rel="noopener noreferrer">Leer más</a>}
          </div>
        ))}
      </div>
    </main>
  );
}
